/* eslint-disable react-hooks/exhaustive-deps */
import { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { setProject } from "../redux/EditorSlice";
import { defaultPythonProject } from "../utils/defaultProjects";
import { useTranslation } from "react-i18next";

export const useProject = ({
  projectIdentifier = null,
  code = null,
  accessToken = null,
}) => {
  const getCachedProject = () =>
    //Lade den Wert von project aus dem LocalStorage
    JSON.parse(localStorage.getItem("project"));
  const [cachedProject, setCachedProject] = useState(getCachedProject());

  const { i18n } = useTranslation();
  const dispatch = useDispatch();

  const loadCachedProject = () => {
    dispatch(setProject(cachedProject));
  };

  projectIdentifier = cachedProject?.identifier || "new";

  useEffect(() => {
    setCachedProject(getCachedProject());
  }, [projectIdentifier]);

  useEffect(() => {
    const is_cached_saved_project =
      projectIdentifier &&
      cachedProject &&
      cachedProject.identifier === projectIdentifier;
    const is_cached_unsaved_project = !projectIdentifier && cachedProject;

    if (is_cached_saved_project || is_cached_unsaved_project) {
      loadCachedProject();
      return;
    }

    //Lese aus dem LocalStorage den Wert von project aus und lese den project_type aus. Wenn project_type = python ist, dann setze das Projekt auf defaultPythonProject, wenn es html ist, dann setze es auf defaultHtmlProject
    if (cachedProject && cachedProject.type === "python") {
      //Lade die Daten aus dem project, wie {"project_type":"html","locale":"en","name":"Blank HTML & CSS Starter","user_id":null,"components":[{"id":"e732f181-933f-4324-844a-c05cedd9c56c","name":"index","extension":"html","content":"test"},{"id":"b06d109f-71e4-4227-8bce-fb67a9599381","name":"styles","extension":"css","content":""}],"image_list":[]}
      const project = {
        name: cachedProject.name,
        type: cachedProject.project_type,
        components: [
          //Schleife ber alle components
          ...cachedProject.components.map((component) => {
            return {
              name: component.name,
              extension: component.extension,
              content: component.content,
            };
          }),
        ],
      };
      dispatch(setProject(project));
      return;
    }
    if (cachedProject && cachedProject.type === "html") {
      const project = {
        name: cachedProject.name,
        type: cachedProject.project_type,
        components: [
          //Schleife ber alle components
          ...cachedProject.components.map((component) => {
            return {
              name: component.name,
              extension: component.extension,
              content: component.content,
            };
          }),
        ],
      };
      dispatch(setProject(project));
      return;
    }
    const data = defaultPythonProject;
    dispatch(setProject(data));
  }, [projectIdentifier, cachedProject, i18n.language, accessToken]);
};
